<template>
  <v-row>
    <v-col cols="6" sm="4" md="2" class="pb-0 pt-md-0 mb-md-4">
      <count-item :value="barbershops" label="Barbearias" />
    </v-col>
    <v-col cols="6" sm="4" md="2" class="pb-0 pt-md-0 mb-md-4">
      <count-item :value="barbers" label="Barbeiros" />
    </v-col>
    <v-col cols="6" sm="4" md="2" class="pb-0 pt-md-0 mb-md-4">
      <count-item :value="clients" label="Usuários" />
    </v-col>
    <v-col cols="6" sm="4" md="2" class="pb-0 pt-md-0 mb-md-4">
      <count-item :value="helps" label="Dúvidas" />
    </v-col>
    <v-col cols="6" sm="4" md="2" class="pb-0 pt-md-0 mb-md-4">
      <count-item :value="indications" label="Indicações" />
    </v-col>
    <v-col cols="6" sm="4" md="2" class="pb-0 pt-md-0 mb-md-4">
      <count-item :value="administrators" label="Administradores" />
    </v-col>
  </v-row>
</template>

<script>
import CountItem from '@/components/moderators/CountItem'

export default {
  components: { CountItem },
  data: () => ({
    barbershops: 0,
    barbers: 0,
    clients: 0,
    indications: 2,
    helps: 10,
    administrators: 3
  }),
  mounted () {
    this.getTotes()
  },
  methods: {
    getTotes () {
      this.$axios(`${this.$url_api}/administrator/dashboard/total`)
      .then(response => {
        if (response.status === 200) {
          if (response.data.codigo === 200) {
            const data = response.data.corpo;
            this.barbershops = data.barbershops;
            this.barbers = data.barbers;
            this.clients = data.clients;
            this.indications = data.indications;
            this.helps = data.helps;
            this.administrators = data.administrators;
          }
        }
      })
    },
  }
}
</script>
